import React from 'react';
import './about.css';
import { Container, Row, Col } from 'reactstrap';
//import aboutImg from "../../assests/images/about-us.png";
import CountUp from 'react-countup';
import './about.css';
import learningkids from '../../assests/lotties/learningkids.json';
//import school from "../../assests/lotties/school.json";
import DisplayLottie from '../DisplayLottie/DisplayLottie';

const AboutUs = () => {
  const aboutUsDetails = [
    {
      lastNumber: 400,
      description: 'Students',
      suffix: '+'
    },
    {
      lastNumber: 20,
      description: 'Staffs',
      suffix: '+'
    },
    {
      lastNumber: 100,
      description: 'Satisfaction',
      suffix: '%'
    }
  ];
  return (
    <section>
      <Container id="about-us">
        <h2 className="text-center mb-4">About Us</h2>
        <Row>
          <Col lg="6" md="6">
            <div className="about__img">
              <DisplayLottie animationData={learningkids} />
            </div>
          </Col>

          <Col lg="6" md="6">
            <div className="about__content text-xs-center">
              <p>
                Lorem ipsum dolor, sit amet consectetur adipisicing elit. Excepturi cupiditate animi
                deserunt libero nesciunt corporis explicabo nobis ex quo molestiae!
              </p>
              <Row>
                {aboutUsDetails.map((aboutUsDetail) => (
                  <Col xs="6">
                    <div className="single__counter">
                      <span className="counter">
                        <CountUp
                          start={0}
                          end={aboutUsDetail.lastNumber}
                          duration={2}
                          suffix={aboutUsDetail.suffix}
                        />
                      </span>

                      <p className="counter__title">{aboutUsDetail.description}</p>
                    </div>
                  </Col>
                ))}
              </Row>
              {/* <div className="about__counter text-xs-center">
								<div className=" d-flex gap-5 align-items-center">
									<div className="single__counter">
										<span className="counter">
											<CountUp start={0} end={101} duration={2} suffix="%" />
										</span>

										<p className="counter__title">Dedication</p>
									</div>

									<div className="single__counter">
										<span className="counter">
											<CountUp start={0} end={1500} duration={2} suffix="" />
										</span>

										<p className="counter__title">Students Around World</p>
									</div>
								</div>

								<div className=" d-flex gap-5 align-items-center">
									<div className="single__counter">
										<span className="counter">
											<CountUp start={0} end={17} duration={2} suffix="" />
										</span>

										<p className="counter__title">Years Served</p>
									</div>

									<div className="single__counter">
										<span className="counter">
											<CountUp start={0} end={100} duration={2} suffix="%" />
										</span>

										<p className="counter__title">Fun</p>
									</div>
								</div>
							</div> */}
            </div>
          </Col>
        </Row>
      </Container>
    </section>
  );
};

export default AboutUs;
