import React from 'react';
import { Container, Row, Col, ListGroup, ListGroupItem } from 'reactstrap';

import './footer.css';

const footerQuickLinks = [
  {
    display: 'Home',
    url: '/'
  },
  {
    display: 'Admission',
    url: 'https://forms.office.com/r/HS7x2HDXdM',
    target: '_noblank'
  },

  {
    display: 'Gallery',
    url: 'gallery'
  },

  {
    display: 'Notices',
    url: 'notices'
  }
];

const footerInfoLinks = [
  {
    display: 'About Us',
    url: '/#about-us'
  },
  {
    display: 'Contact Us',
    url: 'contact-us'
  },

  {
    display: 'Privacy Policy',
    url: '/#privacy-policy'
  },

  {
    display: 'Terms of Service',
    url: '/#terms-of-service'
  }
];

const Footer = () => {
  return (
    <footer className="footer">
      <Container>
        <Row>
          <Col lg="3" md="6" className="mb-4">
            <h2 className=" d-flex align-items-center gap-1">
              <i className="ri-pantone-line"></i> Blooming Buds.
            </h2>

            <div className="follows">
              <p className="mb-0">Follow us on social media</p>
              <span>
                {' '}
                <a href="facebook.com">
                  <i className="ri-facebook-line"></i>
                </a>
              </span>

              <span>
                {' '}
                <a href="facebook.com">
                  <i className="ri-instagram-line"></i>
                </a>
              </span>

              <span>
                {' '}
                <a href="facebook.com">
                  <i className="ri-linkedin-line"></i>
                </a>
              </span>

              <span>
                {' '}
                <a href="facebook.com">
                  <i className="ri-twitter-line"></i>
                </a>
              </span>
            </div>
          </Col>

          <Col lg="3" md="6" className="mb-4">
            <h6 className="fw-bold">Explore</h6>
            <ListGroup className="link__list">
              {footerQuickLinks.map((item, index) => (
                <ListGroupItem key={index} className="border-0 ps-0 link__item">
                  {' '}
                  <a href={item.url} target={item.target}>
                    {item.display}
                  </a>
                </ListGroupItem>
              ))}
            </ListGroup>
          </Col>

          <Col lg="3" md="6" className="mb-4">
            <h6 className="fw-bold">Information</h6>
            <ListGroup className="link__list">
              {footerInfoLinks.map((item, index) => (
                <ListGroupItem key={index} className="border-0 ps-0 link__item">
                  {' '}
                  <a href={item.url}>{item.display}</a>
                </ListGroupItem>
              ))}
            </ListGroup>
          </Col>

          <Col lg="3" md="6">
            <h6 className="fw-bold">Get in Touch</h6>

            <p>Address: Shivapurgadhi, Binayi Triveni - 7, Nawalparasi</p>
            <p>
              {' '}
              Phone:{' '}
              <a href="tel:+977-(0)78-560285" className="text-decoration-none">
                +977-(0)78-560285
              </a>{' '}
            </p>
            <p>
              Email:{' '}
              <a
                href="mailto:info@bloomingbuds.edu.np"
                target="_noblank"
                className="text-decoration-none"
              >
                info@bloomingbuds.edu.np
              </a>
            </p>
          </Col>
        </Row>
      </Container>
    </footer>
  );
};

export default Footer;
