import React from 'react';
import { Container, Row, Col } from 'reactstrap';
// import heroImg from "../../assests/images/hero-img1.png";
// import teaching from '../../assests/images/teaching.gif';
import './hero-section.css';
// import teaching from '../../assests/lotties/teaching.json';
import school from '../../assests/lotties/school.json';
import DisplayLottie from '../../components/DisplayLottie/DisplayLottie';

const HeroSection = () => {
	return (
		<section>
			<Container>
				<Row>
					<Col lg="6" md="6">
						<div className="hero__content text-xs-center">
							<h2 className="mb-4 hero__title">
								Get Enrolled Today
								<h5 className="mb-4 ">where education matters...</h5>
							</h2>
							<p className="mb-5">
								consectetur <br /> adipisicing elit. Aut saepe voluptatum earum delectus <br /> deserunt id iste, quas officiis et repellat!
							</p>
						</div>
						<Row className="justify-content-xs-center">
							<Col xs="6" md="4" className="text-xs-center">
								<a href="https://forms.office.com/r/HS7x2HDXdM" target="_noblank">
									<button className="btn">Admission</button>
								</a>
							</Col>
							<Col xs="6" md="4" className="text-xs-center">
								<a href="/contact-us">
									<button className="btn">Contact Us</button>
								</a>
							</Col>
						</Row>
					</Col>
					<Col lg="6" md="6">
						<DisplayLottie animationData={school} />
					</Col>
				</Row>
			</Container>
		</section>
	);
};

export default HeroSection;
