import React from 'react';
import './testimonial.css';
import { Container, Row, Col } from 'reactstrap';
import Slider from 'react-slick';
import DisplayLottie from '../DisplayLottie/DisplayLottie';
// import img from "../../assests/images/testimonial01.png";
import speech from '../../assests/lotties/speech.json';

const Testimonials = () => {
	const settings = {
		infinite: true,
		dots: true,
		speed: 500,
		slidesToShow: 1,
		autoplay: true,
		autoplaySpeed: 3000,
		slidesToScroll: 1
	};
	return (
		<section>
			<Container>
				<h2 className="mb-4 text-center">Message from the Principal</h2>
				<Row>
					<Col lg="10" md="12" className="m-auto">
						<div className="testimonial__wrapper d-flex justify-content-between align-items-center ">
							<div className="testimonial__img w-100">
								{/* <img src={img} alt="" className="w-100" /> */}
								<DisplayLottie animationData={speech} />
							</div>

							<div className="testimonial__content w-100">
								<Slider {...settings}>
									<div>
										<div className="single__testimonial text-center px-2">
											<h6 className="mb-3 fw-bold">Excellent course of materials</h6>
											<p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Facilis saepe id voluptas molestiae. Aperiam corrupti voluptas earum at molestiae neque!</p>

											<div className="student__info mt-4">
												<h6 className="fw-bold">Santosh Rauniyar</h6>
												<p>Shivapurgadhi, Nawalparasi</p>
											</div>
										</div>
									</div>
								</Slider>
							</div>
						</div>
					</Col>
				</Row>
			</Container>
		</section>
	);
};

export default Testimonials;
